.skills-container {
  width: 100%;
  position: relative; }
  .skills-container .skills-content {
    padding-bottom: 50px;
    width: 1210px;
    margin: auto;
    background: #DD614A;
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center; }
    .skills-container .skills-content .skills-title {
      padding: 20px 0 20px 0; }
      .skills-container .skills-content .skills-title h4 {
        color: white;
        font-family: 'Sriracha', italic;
        font-size: 42px;
        letter-spacing: 1px;
        font-weight: 400;
        margin: 0; }
    .skills-container .skills-content .skills-pills-container {
      margin: 0 10px;
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      align-items: center;
      justify-content: space-around; }
      .skills-container .skills-content .skills-pills-container .skills-pill-container {
        overflow-wrap: break-word;
        margin: 10px 10px;
        font-family: 'Roboto Condensed', sans-serif;
        background-color: #eba194;
        box-shadow: inset 0 0 3px 0 black, inset 0 0 0 10px #f7d9d4;
        height: 200px;
        width: 115px;
        border-radius: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center; }
        .skills-container .skills-content .skills-pills-container .skills-pill-container img {
          width: 64px;
          height: 64px;
          background-color: #f7d9d4;
          margin-top: 20px;
          padding: 5px;
          border-radius: 25px; }
        .skills-container .skills-content .skills-pills-container .skills-pill-container div:nth-child(3) {
          margin-bottom: 20px; }

@media (max-width: 1300px) and (min-width: 1200px) {
  .skills-content {
    width: 92.6% !important; } }

@media (max-width: 1200px) {
  .skills-content {
    width: 100% !important; } }

@media (max-width: 1120px) and (min-width: 450px) {
  .skills-pill-container {
    margin: 10px 55px !important; } }

@media (max-width: 449px) {
  .skills-pill-container {
    margin: 10px 20px !important; } }
