.aboutme-container {
  margin-top: 50px;
  width: 100%;
  position: relative; }
  .aboutme-container .aboutme-title {
    white-space: nowrap;
    font-family: 'Sriracha', cursive;
    font-size: 25px;
    background: white;
    box-shadow: inset 0 0 0 10px #DD614A, 0 0 0 10px white;
    padding: 30px 50px;
    position: absolute;
    top: -55px;
    left: 50%;
    transform: translateX(-50%); }
    .aboutme-container .aboutme-title h4 {
      margin: 0; }
  .aboutme-container .aboutme-content {
    background: linear-gradient(135deg, #DD614A 5%, transparent 5%), linear-gradient(-135deg, #DD614A 5%, transparent 5%);
    width: 1210px;
    margin: auto;
    box-shadow: inset 0 5px 0 5px #DD614A, inset 5px 5px 0 5px #DD614A, inset -5px 5px 0 5px #DD614A; }
    .aboutme-container .aboutme-content .aboutme-text {
      font-family: 'Roboto Condensed', sans-serif;
      font-size: 20px;
      padding: 70px 100px 30px 100px; }
      .aboutme-container .aboutme-content .aboutme-text p {
        text-align: justify;
        letter-spacing: 0.5px;
        margin: 0; }

@media (max-width: 1300px) and (min-width: 1200px) {
  .aboutme-content {
    width: 92.6% !important; } }

@media (max-width: 1200px) {
  .aboutme-content {
    width: 100% !important; } }

@media (max-width: 700px) {
  .aboutme-text {
    padding: 70px 40px 30px 40px !important; } }
