.contact-title-container {
  background-color: #f7d9d4;
  display: flex;
  justify-content: center; }
  .contact-title-container .contact-title-content {
    z-index: 5;
    width: 1200px;
    height: 100px;
    background: #DD614A;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-family: 'Sriracha', cursive;
    font-size: 42px;
    letter-spacing: 1px; }
    .contact-title-container .contact-title-content h4 {
      font-weight: 400;
      margin: 0; }

.contact-container {
  display: flex;
  justify-content: center;
  background: #f7d9d4;
  height: 200px; }
  .contact-container .contact-content {
    margin: 0 20px;
    width: 1200px;
    height: 100%;
    margin: auto;
    display: flex;
    justify-content: space-around;
    align-items: center; }
    .contact-container .contact-content .contact-direct {
      height: 70px;
      display: flex;
      justify-content: space-between; }
      .contact-container .contact-content .contact-direct .contact-direct-item {
        text-decoration: none;
        display: flex;
        align-items: center; }
        .contact-container .contact-content .contact-direct .contact-direct-item i {
          text-decoration: none;
          padding-right: 10px; }
        .contact-container .contact-content .contact-direct .contact-direct-item span {
          color: black;
          font-size: 19px;
          letter-spacing: 0.5px;
          font-family: 'Roboto Condensed', sans-serif; }
    .contact-container .contact-content .contact-social img {
      margin: 0 10px;
      width: 32px;
      height: 32px; }

@media (max-width: 1200px) {
  .contact-content {
    width: 100% !important; } }

.md-dark {
  color: #197278; }
