.left-slope, .right-slope, .left-slope-flipped, .right-slope-flipped {
  width: 50px;
  height: 100px; }

.left-slope {
  background: linear-gradient(70deg, transparent 0 42px, #DD614A 43px 50px); }

.right-slope {
  background: linear-gradient(-70deg, transparent 0 42px, #DD614A 43px 50px); }

.left-slope-flipped {
  background: linear-gradient(110deg, transparent 0 42px, #DD614A 43px 50px); }

.right-slope-flipped {
  background: linear-gradient(-110deg, transparent 0 42px, #DD614A 43px 50px); }

.md-white {
  color: white; }

.banner-wrapper {
  display: flex;
  justify-content: center; }

@media (max-width: 1200px) {
  .left-slope, .right-slope, .left-slope-flipped, .right-slope-flipped {
    display: none; } }

.banner-container {
  height: 100px;
  width: 1200px;
  background: #DD614A;
  display: flex; }
  .banner-container .banner-content {
    height: 70px;
    width: 90%;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
    .banner-container .banner-content img {
      width: 90px;
      height: 70px; }
    .banner-container .banner-content .banner-contact-container {
      display: flex;
      flex-direction: column;
      justify-content: space-between; }
      .banner-container .banner-content .banner-contact-container .banner-contact-item {
        display: flex;
        align-items: center;
        text-decoration: none; }
        .banner-container .banner-content .banner-contact-container .banner-contact-item span {
          font-family: 'Sriracha', cursive;
          color: white;
          font-size: 20px;
          letter-spacing: 1px; }
        .banner-container .banner-content .banner-contact-container .banner-contact-item i {
          text-decoration: none;
          padding-right: 10px; }

@media (max-width: 423px) {
  .banner-content img {
    width: 55px !important;
    height: 50px !important; }
  .banner-content span {
    font-size: 15px !important; }
  .banner-container {
    height: 80px; }
    .banner-container .banner-content {
      align-content: center; } }
