.heropage-container {
  padding: 100px 0;
  width: 1200px;
  margin: auto; }
  .heropage-container .heropage-content {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap-reverse; }
    .heropage-container .heropage-content .heropage-leftside {
      padding-bottom: 30px;
      padding-left: 20px;
      padding-right: 20px;
      font-family: 'Sriracha', cursive; }
      .heropage-container .heropage-content .heropage-leftside .heropage-socials {
        display: flex;
        justify-content: flex-end;
        padding-top: 20px; }
        .heropage-container .heropage-content .heropage-leftside .heropage-socials img {
          padding: 0 10px;
          height: 32px;
          width: 32px; }
      .heropage-container .heropage-content .heropage-leftside .heropage-firstline {
        display: flex;
        align-items: center;
        font-size: 22px;
        margin-bottom: 0; }
        .heropage-container .heropage-content .heropage-leftside .heropage-firstline .heropage-hello {
          border-radius: 5px;
          color: white;
          background-color: #DD614A;
          padding: 5px 10px;
          margin-right: 10px; }
      .heropage-container .heropage-content .heropage-leftside .heropage-name {
        margin: 0;
        font-size: 50px;
        line-height: 1.2; }
      .heropage-container .heropage-content .heropage-leftside .heropage-description {
        letter-spacing: 0.5px;
        font-size: 20px;
        margin: 0; }
    .heropage-container .heropage-content .heropage-rightside {
      display: flex; }
      .heropage-container .heropage-content .heropage-rightside .heropage-image {
        display: flex;
        justify-content: center;
        width: 400px;
        height: 500px; }
        .heropage-container .heropage-content .heropage-rightside .heropage-image img {
          width: 300px;
          height: 404px; }

@media (max-width: 1200px) {
  .heropage-container {
    display: flex;
    width: unset; } }

@media (max-width: 874px) {
  .heropage-container {
    padding: 0;
    padding-bottom: 50px; } }

@media (max-width: 474px) {
  .heropage-name {
    text-align: center; }
  .heropage-description {
    text-align: center; } }

@media (max-width: 420px) {
  .heropage-description {
    font-size: 15px !important; }
  .heropage-image {
    align-items: flex-start !important;
    width: 100% !important;
    height: 290px !important; }
    .heropage-image img {
      width: 200px !important;
      height: 269px !important; } }
