.pslider-title-container {
  display: flex;
  justify-content: center; }
  .pslider-title-container .pslider-title-content {
    width: 1200px;
    background: #DD614A;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-family: 'Sriracha', italic;
    font-size: 42px;
    letter-spacing: 1px; }
    .pslider-title-container .pslider-title-content h4 {
      font-weight: 400;
      margin: 0; }

.pslider-stripe-container {
  background-color: #f7d9d4;
  height: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: visible; }
  .pslider-stripe-container .active {
    background-color: #DD614A; }
  .pslider-stripe-container div {
    transition: background-color 200ms ease;
    cursor: pointer;
    border-radius: 50%;
    margin: 0 10px;
    background-color: #f7d9d4;
    z-index: 20;
    width: 20px;
    height: 20px;
    box-shadow: inset 0 0 0 3px #f7d9d4; }
    .pslider-stripe-container div:hover {
      background-color: #197278; }

.pslider-container {
  font-family: 'Roboto Condensed', sans-serif;
  width: 100%;
  padding-bottom: 50px;
  background: #DD614A; }
  .pslider-container .pslider-content {
    width: 1000px;
    margin: auto; }
    .pslider-container .pslider-content .pslider-title-bar {
      height: 50px;
      display: flex;
      justify-content: space-between;
      align-items: center; }
      .pslider-container .pslider-content .pslider-title-bar .pslider-project-link {
        width: 39px; }
        .pslider-container .pslider-content .pslider-title-bar .pslider-project-link img {
          width: 39px;
          height: 50px; }
      .pslider-container .pslider-content .pslider-title-bar .pslider-project-counterweight {
        width: 39px; }
      .pslider-container .pslider-content .pslider-title-bar .pslider-project-title {
        text-align: center;
        font-size: 32px;
        display: flex;
        justify-content: center; }
    .pslider-container .pslider-content .pslider-project {
      width: 100%;
      margin-top: 10px;
      display: flex;
      justify-content: space-around; }
      .pslider-container .pslider-content .pslider-project .pslider-project-description {
        border-radius: 25px;
        width: 100%;
        height: 300px;
        background: #f7d9d4; }
        .pslider-container .pslider-content .pslider-project .pslider-project-description textarea {
          cursor: default;
          font-family: 'Roboto Condensed', sans-serif;
          border: none;
          width: calc(100% - 40px);
          height: calc(100% - 40px);
          background: none;
          resize: none;
          font-size: 20px;
          margin: 0;
          padding: 20px;
          letter-spacing: 0.5px; }
          .pslider-container .pslider-content .pslider-project .pslider-project-description textarea:focus {
            outline: 0; }

.progress-bar-container {
  background: #f7d9d4;
  border-radius: 25px;
  width: 300px;
  height: 100px;
  text-align: center; }
  .progress-bar-container .progress-bar {
    line-height: 25px;
    font-family: 'Sriracha', cursive;
    font-size: 22px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 25px;
    width: 100%;
    height: 100%;
    box-shadow: inset 0 0 0 5px #f7d9d4; }
  .progress-bar-container span:nth-child(2) {
    font-size: 18px; }

.pslider-image-slider {
  display: flex;
  width: 100%;
  justify-content: space-around;
  flex-wrap: wrap;
  position: relative;
  align-items: center; }
  .pslider-image-slider .pslider-project-progress {
    width: 25%;
    display: flex;
    justify-content: flex-end; }
  .pslider-image-slider .pslider-project-technologies {
    background: #f7d9d4;
    border-radius: 25px;
    width: 25%; }
    .pslider-image-slider .pslider-project-technologies .pslider-project-technologies-content {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 15px;
      flex-wrap: wrap; }
      .pslider-image-slider .pslider-project-technologies .pslider-project-technologies-content .pslider-project-technology {
        font-size: 18px;
        letter-spacing: 0.5px;
        color: white;
        background: #DD614A;
        padding: 0 5px;
        border-radius: 5px;
        margin: 2px 3px; }
  .pslider-image-slider .pslider-project-image {
    background: red;
    position: relative;
    cursor: pointer;
    margin: 5px;
    border-radius: 25px;
    height: 180px;
    width: 180px;
    background: #f7d9d4;
    display: flex;
    justify-content: center;
    align-items: center; }
    .pslider-image-slider .pslider-project-image img {
      transition: all 300ms ease;
      display: block;
      max-width: 90%;
      max-height: 90%;
      width: auto;
      height: auto; }
    .pslider-image-slider .pslider-project-image i {
      position: absolute;
      bottom: 5px;
      right: 5px; }

@media (max-width: 1200px) {
  .pslider-content {
    width: 90% !important; }
  .pslider-title-bar {
    width: 100% !important; } }

@media (max-width: 600px) {
  .pslider-image-slider div:nth-child(1) {
    order: 2; }
  .pslider-image-slider div:nth-child(2) {
    order: 3; }
  .pslider-image-slider div:nth-child(3) {
    order: 1; }
  .pslider-image-slider .pslider-project-progress {
    width: 100%; }
    .pslider-image-slider .pslider-project-progress .progress-bar-container {
      width: 100%;
      height: 50px; }
      .pslider-image-slider .pslider-project-progress .progress-bar-container .progress-bar {
        flex-direction: row; }
        .pslider-image-slider .pslider-project-progress .progress-bar-container .progress-bar span {
          padding: 0 5px; }
  .pslider-image-slider .pslider-project-technologies {
    flex: 1; }
  .pslider-image-slider .pslider-project-image {
    width: 60%; }
  .pslider-container {
    height: 570px; }
  .pslider-project-link {
    width: 25px !important; }
    .pslider-project-link img {
      width: 25px !important;
      height: 30px !important; }
  .pslider-project {
    margin: 0 !important; }
  .pslider-project-counterweight {
    width: 25px !important; } }

@media (max-width: 600px) {
  .pslider-project-description textarea {
    font-size: 15px !important;
    letter-spacing: 1px !important; } }

.slide-enter-toleft {
  opacity: 0;
  transform: translateX(150%); }

.slide-enter-toright {
  opacity: 0;
  transform: translateX(-150%); }

.slide-enter-active {
  opacity: 1;
  transform: translateX(0%); }

.slide-exit {
  transform: translateX(0%); }

.slide-exit-active-toleft {
  opacity: 0;
  transform: translateX(-150%); }

.slide-exit-active-toright {
  opacity: 0;
  transform: translateX(150%); }

.slide-enter-active,
.slide-exit-active-toleft,
.slide-exit-active-toright {
  transition: opacity 500ms ease-in-out, transform 500ms ease-in-out; }
